import React from 'react';
import queryString from 'query-string';
import { QRious } from 'react-qrious';
import './style.css';
import { Link, Navigate, useLocation } from 'react-router-dom';

export const MainPage = () => {
    const { search } = useLocation();
    const params = queryString.parse(search);

    if (params.goto) {
        return <Navigate to={`/qrme/create/text?${
            queryString.stringify({
                ...params,
                view: 1,
                goto: '',
            })
        }`} />;
    }

    if (!params.type || !params.value) {
        return <Navigate to="/qrme/create/text?type=text" />;
    }

    const nextGoTo = `?${queryString.stringify({
        ...params,
        view: 1,
        goto: '1',
    })}`;

    return (
        <div className="main-page">
            <div className="wrapper">
                <QRious
                    value={`${window.location.origin}?${nextGoTo}`}
                    size={Math.min(320, window.innerWidth - 60)}
                />
            </div>
            <Link className="edit-link" to={`/qrme/create/${params.type}${search}`}>Редактировать</Link>
        </div>
    );
}
